import getArticles from '@polyblog/polyblog-js-client';
import { useLoaderData } from 'remix';

let locale = 'en';
export const loader = async () => {
  const data = {
    articles: await getArticles({
      organizationId: 'c398463407b5c12f27f9aed4',
      project: 'polyblog',
      published: true,
      locale,
      sortDirection: 'DESC',
    }),
  };

  console.log(data);
  return data;
};

export default function BlogPage() {
  const posts = useLoaderData();
  return (
    <>
      <div className="row row-cols-1 row-cols-md-2 g-4">
        {posts.map(
          ({
            _id,
            locale,
            slug,
            coverUrl,
            title,
            author,
            creationTime,
            subtitle,
          }) => (
            <div key={_id} className="col-md-6 col-lg-4 col-12">
              <Link
                key={_id}
                to={`/${locale}/${slug}`}
                className="text-decoration-none"
              >
                <div className="card h-100 shadow">
                  <img src={coverUrl} className="card-img-top" alt={title} />
                  <div className="card-body">
                    <h3 className="card-title my-3 text-dark">{title}</h3>
                    <h5 className="my-3 text-dark">{subtitle}</h5>
                    {/* <p className="text-dark">
                      Posted by {post.authorName} on {post.publishDate}
                    </p> */}
                  </div>
                </div>
              </Link>
            </div>
          ),
        )}
      </div>
    </>
  );
}
